* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #ddcfdc;
  background-color: #9c89f1;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  max-width: 320px;
  margin-bottom: 16px;
  padding: 8px;
}

.search-form {
  justify-content: center;
  align-items: center;
  display: flex;
}

.search-input {
  width: 300px;
  background: #2472cc;
  border-width: 0;
  border-radius: 0;
  padding: 0 20px;
  font-family: Roboto, sans-serif;
  line-height: 40px;
  display: block;
}

.search-btn {
  color: #f95a5a;
  text-transform: uppercase;
  letter-spacing: 2px;
  cursor: pointer;
  background-color: inherit;
  border: 1px solid;
  border-image-slice: .5;
  margin: 10px 20px;
  padding: 15px 30px;
  font-family: Montserrat, sans-serif;
  text-decoration: none;
  transition: all .8s cubic-bezier(.165, .84, .44, 1);
  display: inline-block;
  position: relative;
  overflow: hidden;
}

.search-btn:before {
  content: "";
  height: 0;
  width: 100%;
  z-index: -1;
  color: #fff;
  background: linear-gradient(#ff3000, #ed0200, #ff096c, #d50082);
  transition: all .8s cubic-bezier(.165, .84, .44, 1);
  position: absolute;
  top: 0;
  left: 0;
}

.search-btn:hover {
  background: rgba(255, 255, 255, 0);
}

.search-btn:hover:before {
  height: 100%;
  top: auto;
  bottom: 0%;
}

.toggle-form {
  justify-content: center;
  align-items: center;
  display: flex;
}

.option-container:not(:last-child) {
  margin-right: 30px;
}

.option-input {
  -webkit-appearance: none;
  -ms-appearance: none;
  appearance: none;
  -o-appearance: none;
  height: 40px;
  width: 40px;
  color: #fff;
  cursor: pointer;
  z-index: 1000;
  background: #cbd1d8;
  border: none;
  border-radius: 50%;
  outline: none;
  margin-right: .5rem;
  transition: all .15s ease-out;
  display: inline-block;
  position: relative;
  top: 13.3333px;
  bottom: 0;
  left: 0;
  right: 0;
}

.option-input:hover {
  background: #9faab7;
}

.option-input:checked {
  background: #40e0d0;
}

.option-input:checked:before {
  width: 40px;
  height: 40px;
  justify-content: center;
  align-items: center;
  font-size: 25px;
  font-weight: bold;
  display: flex;
  position: absolute;
}

.option-input:checked:after {
  content: "";
  z-index: 100;
  background: #40e0d0;
  animation: click-wave .65s;
  display: block;
  position: relative;
}

.option-input:after {
  border-radius: 50%;
}

.option-input__text {
  color: #fff;
  text-transform: uppercase;
  font-family: Montserrat, sans-serif;
  overflow: hidden;
}

@keyframes click-wave {
  0% {
    height: 40px;
    width: 40px;
    opacity: .35;
    position: relative;
  }

  100% {
    height: 200px;
    width: 200px;
    opacity: 0;
    margin-top: -80px;
    margin-left: -80px;
  }
}

.load-more {
  width: 200px;
  color: #fff;
  cursor: pointer;
  background-image: linear-gradient(45deg, #6ab1d7 0%, #33d9de 50%, #002878 100%);
  background-position: 99% 0;
  background-size: 200% 200%;
  border-radius: 30px;
  margin-left: auto;
  margin-right: auto;
  padding: 10px 30px;
  font-family: Montserrat, sans-serif;
  font-size: 24px;
  font-weight: 300;
  text-decoration: none;
  transition: all .5s;
  display: inline-block;
  box-shadow: 0 16px 32px rgba(0, 40, 120, .35);
}

.load-more:hover {
  background-position: 0 0;
  box-shadow: 0 0 rgba(0, 40, 120, 0);
}

.load-more.is-hidden {
  display: none;
}

